





























import { Component, Vue } from "vue-property-decorator";
import {FlyErrResponse, FlyResponse} from "flyio";

@Component({
    components: {
    }
})
class ListPage extends Vue {
    private imgList: any[] = [];
    // 是否正在刷新
    private isRefreshing: boolean = false;
    private isLoading: boolean = false;
    private pageIndex: number = 0;

    // 重新获取图片列表
    async refreshImgList() {
        this.isRefreshing = true;
        this.pageIndex = 0;
        this.imgList = [];
        await this.getImgList();
        this.isRefreshing = false;
    }
    // 加载更多图片列表
    async loadingImgList() {
        this.isLoading = true;
        this.pageIndex = this.imgList.length % 10 != 0 ? parseInt(this.imgList.length / 10 + 1 + '') : parseInt(this.imgList.length / 10  + '');
        await this.getImgList();
        this.isLoading = false;
        console.log(this.pageIndex, '下标', this.imgList.length);
    }
    /**
     * 获取图片列表
     * @param pageSize 每次获取的数据数量
     * */
    getImgList(pageSize: number = 10): Promise<any> {
        return new Promise((resolve: Function, reject: Function) => {
            this.$fly.get('/api/img', {
                page_index: this.pageIndex,
                page_size: pageSize,
            })
            .then((res: FlyResponse) => {
                this.imgList.push(...res.data.data);
                resolve(res.data);
            })
            .catch((err: FlyErrResponse) => {
                reject(err);
            })
        })
    }

    mounted(): void {
        this.getImgList();
    }
}
export default ListPage;
