
































import { Component, Vue } from 'vue-property-decorator';
import AppBar from "@/components/AppBar.vue";

@Component({
  components: {
    AppBar,
  }
})
class App extends Vue {
  private openDrawer: boolean = false;
  private isLogin: boolean = false;

  /**
   * 切换侧边菜单开关状态
   * @param {Boolean} isOpen ture:开启  false:关闭
   * */
  changeDrawerState(isOpen: boolean): void {
      this.openDrawer = isOpen;
  }

  mounted(): void {
    console.log('是否执行');
    this.isLogin = !!sessionStorage.getItem('token') && sessionStorage.getItem('token') != '';
  }

  created(): void {
    if (!window.navigator.userAgent.includes('Mobile')) {
      this.$toast.error({
        message: "建议使用移动设备访问",
        position: "top",
        close: false,
      })
    }
  }
}
export default App;
