

















































import {Component, Vue} from 'vue-property-decorator';
import {FlyErrResponse, FlyResponse} from "flyio";

interface RegisterForm {
    account: string;
    email: string;
    password: string;
    code: string;
}

@Component
class RegisterPage extends Vue {
    private registerForm: RegisterForm = {
        account: '',
        email: '',
        password: '',
        code: '',
    };

    private visibilityState: boolean = false;
    private sendCodeText: string = '发送验证码';

    private passwordRules: any[] = [
        {validate: (val: any) => !!val, message: '请填写密码'},
        {validate: (val: any) => val.length >= 6, message: '密码长度不能少于6位'}
    ];

    private emailRules: any[] = [
        {validate: (val: any) => !!val, message: '请填写邮箱'},
    ];

    private codeRules: any[] = [
        {validate: (val: any) => !!val, message: '请填写验证码'},
    ];

    private accountRules: any[] = [
        {validate: (val: any) => !!val, message: '请填写账号'},
        {validate: (val: any) => val.length >= 6, message: '账号长度不能少于6位'},
    ];

    sendCodeTimer(): void {
        let i: number = 60;
        this.sendCodeText = `还剩${i}秒`;
        const timer: any = setInterval(() => {
            i--;
            this.sendCodeText = `还剩${i}秒`;
            if (i <= 0) {
                this.sendCodeText = '发送验证码';
                clearInterval(timer)
            }
        }, 1000)
    }

    // 获取验证码
    getCaptchaCode(): void {
        if (this.registerForm.email == '') {
            this.$toast.error('请输入邮箱')
            return;
        }
        this.sendCodeTimer();
        this.$fly.get('/api/email', {
            email: this.registerForm.email,
        })
        .then((res: FlyResponse) => {
            if (res.data.status == 'error') {
                this.$toast.error(res.data.message);
                return;
            }
            this.$toast.success(res.data.message);
        })
        .catch((err: FlyErrResponse) => {
            this.$toast.error(`验证码获取错误:${JSON.stringify(err)}`)
        })
    }

    // 提交注册信息
    submit(): void {
        if (this.registerForm.account == '') {
            this.$toast.error('请输入账号')
            return;
        }
        if (this.registerForm.password == '') {
            this.$toast.error('请输入密码')
            return;
        }
        if (this.registerForm.email == '') {
            this.$toast.error('请输入邮箱')
            return;
        }
        if (this.registerForm.code == '') {
            this.$toast.error('请输入验证码')
            return;
        }
        this.$fly.post('/api/register', this.registerForm)
            .then((res: FlyResponse) => {
                if (res.data.status == 'error') {
                    this.$toast.error(res.data.message);
                    return;
                }
                this.$router.back();
                this.$toast.success(res.data.message);
            })
            .catch((err: FlyErrResponse) => {
                this.$toast.error(`注册错误:${JSON.stringify(err)}`)
            })
    }
}
export default RegisterPage;
