



















import {Component, Vue} from 'vue-property-decorator';
import {FlyErrResponse, FlyResponse} from "flyio";

@Component
class FeedbackPage extends Vue {
    private feedbackForm: any = {
        note: '',
    }

    private noteRules: any[] = [
        {validate: (val: any) => !!val, message: '请填写内容'},
    ];

    postFeedback(): void {
        this.$fly.post('/api/login', this.feedbackForm)
            .then((res: FlyResponse) => {
                if (res.data.status == 'error') {
                    this.$toast.error(res.data.message);
                    return;
                }
                this.$router.back();
                this.$toast.success(res.data.message);
            })
            .catch((err: FlyErrResponse) => {
                this.$toast.error(`提交错误:${JSON.stringify(err)}`)
            })
    }
}
export default FeedbackPage;
