import Vue from 'vue'
import VueRouter from 'vue-router'
import IndexPage from '@/views/listPage.vue';
import UploadImg from "@/views/UploadImg.vue";
// @ts-ignore
import AboutUs from '@/views/AboutUs';
import FeedbackPage from "@/views/Feedback.vue";
import RegisterPage from "@/views/RegisterPage.vue";
import LoginPage from "@/views/LoginPage.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: IndexPage,
  },
  {
    path: '/upload',
    name: 'uploadImg',
    component: UploadImg,
  },
  {
    path: '/about',
    name: 'about',
    component: AboutUs,
  },
  {
    path: '/feedback',
    name: 'feedback',
    component: FeedbackPage,
  },
  {
    path: '/register',
    name: 'register',
    component: RegisterPage,
  },
  {
    path: '/login',
    name: 'login',
    component: LoginPage,
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router;
