



























import {Component, Prop, Vue} from 'vue-property-decorator';
import {FlyErrResponse, FlyResponse} from "flyio";

interface ImgInfo {
    operation: boolean;
    src: string | ArrayBuffer | null;
}

@Component
class UploadImg extends Vue {

    @Prop({
        type: String,
        default: 'image/*',
    })
    private accept!: string;

    private uploadFileList: ArrayBuffer[] = [];  // 实际上传的图片数组
    private uploadFileSrcList: ImgInfo[] = [];  // 本地展示图片数据


    // 触发选择图片
    selectImg(): void {
        let uploadElement: any = this.$refs.upload;
        uploadElement.click();
    }

    // 本地选中图片
    changeUploadFile(): void {
        let uploadElement: any = this.$refs.upload;
        let fileReader: FileReader = new FileReader();
        this.uploadFileList.push(uploadElement.files[0]);
        fileReader.readAsDataURL(uploadElement.files[0]);
        fileReader.onload = () => {
            this.uploadFileSrcList.push({
                src: fileReader.result,
                operation: false,
            });
        }
    }

    /**
     * 删除图片
     * @param {Number} index 删除的图片的下标
     * */
    removeImg(index: number): void {
        this.uploadFileList.splice(index, 1);
        this.uploadFileSrcList.splice(index, 1);
    }

    // 提交图片数据
    postUploadImg(): void {
        if (this.uploadFileList.length == 0) {
            this.$toast.error({
                close: false,
                message: '请选择图片',
            })
            return ;
        }
        let formData: FormData = new FormData();

        this.uploadFileList.forEach((val: any) => {
            formData.append("uploadFile", val)
        })
        this.$fly.post('/api/upload/multiImg', formData, {
            headers: {
                'Content-Type': 'multipart/form-data',
            },
        })
        .then((res: FlyResponse) => {
            if (res.data.status == 'error') {
                this.$toast.error({
                    close: false,
                    message: res.data.message,
                    // position: 'top'
                });
                return ;
            }
            this.$toast.success({
                close: false,
                message: res.data.message,
                // position: 'top'
            });
            console.log('上传成功:', res);
        })
        .catch((err: FlyErrResponse) => {
            this.$toast.error({
                close: false,
                message: `上传失败:${err}`,
                // position: 'top'
            });
            console.log('上传失败:', err);
        })
    }

    mounted() {
    }
}
export default  UploadImg;
