





















import {Component, Emit, Vue} from 'vue-property-decorator';

@Component
class AppBar extends Vue {
    @Emit("changeDrawerState")
    changeDrawerState(val: boolean): void{}

    /**
     * 处理appbar左侧点击事件
     * */
    navigationLeftHandle(): void {
        if (this.$route.name != 'index') {
            this.$router.go(-1);
        } else {
            this.changeDrawerState(true);
        }
    }
}
export default AppBar;
