import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import { Button, GridList, LoadMore, AppBar, Icon, Snackbar, Drawer, List, Form, TextField } from 'muse-ui';
import 'muse-ui/lib/styles/theme.less';
import 'muse-ui/lib/styles/base.less';
// @ts-ignore
import theme from 'muse-ui/lib/theme';
import fly, {FlyRequestConfig} from 'flyio';
// @ts-ignore
import Toast from 'muse-ui-toast/src/index'



theme.add('pink', {
  primary: '#ea80d1',
  secondary: '#ff4081',
  success: '#4caf50',
  warning: '#ffeb3b',
}, 'light');
theme.use('pink');

Vue.prototype.$fly = fly;
Vue.use(Toast);
Vue.use(Form);
Vue.use(TextField);
Vue.use(Drawer);
Vue.use(Snackbar);
Vue.use(Icon);
Vue.use(AppBar);
Vue.use(List);
Vue.use(LoadMore);
// Vue.use(Paper);
Vue.use(GridList);
Vue.use(Button);
// Vue.use(BottomNav);
Vue.config.productionTip = false

fly.interceptors.request.use((request: FlyRequestConfig)=>{
  if (request.headers['Content-Type'] != 'multipart/form-data' && (request.method == 'POST' || request.method == 'post')) {
    request.headers['Content-Type'] = 'application/x-www-form-urlencoded';
    request.headers['Authorization'] = `Bearer ${sessionStorage.getItem('token')}`;
  }
  return request;
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
